import '@devsnc/sn-catalog-form';
import {SC_WIZARD, TABBED, COMPACT, DEFAULT} from '../constants';
import {catalogWizardMessages} from '../../../messages';
import '@servicenow/now-heading';

const view = (state, {updateState}) => {
	const {properties} = state;
	const {
		sectionData,
		sectionStyle,
		wizardData = {},
		readOnlyMode = '',
		formData,
	} = properties;
	const {name, shortDescription, variablesLayout, questions} = sectionData;
	return (
		<div className={'sn-catalog-wizard-step-section ' + readOnlyMode}>
			<div className={'sn-catalog-wizard-step-section-details ' + sectionStyle}>
				<div className="sn-catalog-wizard-step-section-label">
					{name ? <span>{name}</span> : null}
					{getLockIcon(state, sectionData, updateState)}
				</div>
				{_renderShortDescription(shortDescription)}
				<div className="sn-catalog-wizard-step-section-questions">
					<sn-catalog-form
						formData={formData}
						source-table={SC_WIZARD}
						source-id={wizardData.sysId}
						fields={questions}
						variables-layout={variablesLayout}
						no-gutter="true"
						readOnlyOption={readOnlyMode}
						render-style={
							wizardData.layout == TABBED || sectionData.width == '100'
								? COMPACT
								: DEFAULT
						}
					/>
				</div>
			</div>
		</div>
	);
};

const getLockIcon = (state, sectionData, updateState) => {
	const setTargetRef = (ref) => setTimeout(() => updateState({targetRef: ref}));
	if (isTemplateReadOnly(sectionData)) {
		return (
			<div className="sn-catalog-wizard-step-section-label-lock">
				<now-icon
					icon="lock-outline"
					size="lg"
					hook-insert={({elm}) => setTargetRef(elm)}
					hook-destroy={() => setTargetRef(null)}></now-icon>
				{_renderTooltip(state, catalogWizardMessages.SECTION_LOCK_TOOLTIP)}
			</div>
		);
	}
};

const _renderTooltip = (state, tooltipContent) => {
	if (tooltipContent) {
		return (
			<now-tooltip
				id={`tooltip-${state.componentId}`}
				target-ref={state.targetRef}
				content={tooltipContent}
				position={['bottom-center top-center']}
			/>
		);
	}
};

const isTemplateReadOnly = (sectionData) => {
	const {questions = {}} = sectionData;
	for (let question of Object.values(questions)) {
		const {
			visible = true,
			templateReadonly = false,
			readonly = false,
		} = question;
		if (visible && (!readonly || !templateReadonly)) {
			return false;
		}
	}
	return true;
};

const _renderShortDescription = (shortDescription) => {
	if (shortDescription) {
		return (
			<div className="sn-catalog-wizard-step-section-short-description">
				<span>{shortDescription}</span>
			</div>
		);
	}
};

export default view;
