import {createCustomElement} from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import {view} from './view';
import '@devsnc/sn-catalog-form';
import actionHandlers from './actionHandlers';
import actions from './actions';
import '../sn-catalog-wizard';
import {gFormActionHandlers} from './gFormActions';
import {SC_CART_ITEM} from './constants';
import transformState from './transformState';

createCustomElement('sn-catalog-wizard-connected', {
	renderer: {type: snabbdom},
	properties: {
		sysId: {
			default: '',
		},
		mode: {
			default: '',
		},
		templateId: {
			default: '',
		},
		recordId: {
			default: '',
		},
		parentRecordTable: {
			default: '',
		},
		parentRecordId: {
			default: '',
		},
		isError: {default: false},
		isLoading: {default: true},
		isSuccess: {default: false},
		wizardDetails: {default: {}},
		errorMessage: {default: ''},
		formData: {
			default: {tableName: SC_CART_ITEM, sysId: -1},
			dependencies: ['wizardDetails'],
		},
		targetRecord: {default: {}},
		variables: {default: {}},
		editMode: {
			default: false,
		},
		submitting: {
			default: false,
		},
		formDirty: {
			default: false,
		},
		wizardData: {
			selectable: true,
			default: {
				steps: [],
				stepsList: [],
				sysId: '',
			},
		},
		dataFetchOnly: {
			default: false,
		},
		hideHomeButton: {
			default: false,
		},
		hideTitle: {
			default: false,
		},
		wizardTitle: {
			default: '',
		},
		hidePreviewButton: {
			default: false,
		},
	},
	initialState: {
		wizardMeta: {},
		notificationItems: [],
		/**
		 * Array of properties that should be updated for macro variable field, everytime the property is changed
		 */
		macroVariableUpdateProperties: ['editMode'],
		/**
		 * Maps attachment sysId to variable name
		 */
		attachmentToVariableMap: {},
		/**
		 * Maps attachment variable to contentType of that attachment
		 */
		attachmentContentTypes: {},
	},
	view,
	actions: {
		...actions,
		...gFormActionHandlers.actions,
	},
	actionHandlers: {
		...actionHandlers,
		...gFormActionHandlers.actionHandlers,
	},
	transformState,
});
