import '../sn-catalog-wizard/wizard-step-section';
import '@servicenow/now-accordion';
import '@servicenow/now-heading';
import get from 'lodash/get';
import {cloneDeep} from 'lodash';
import {CATALOG_WIZARD_REVIEW_DESC, REVIEW_AND_SUBMIT} from './constants';

const view = (state) => {
	const {
		properties: {stepData, stepsList, submitting, preview, submitButtonLabel},
	} = state;

	if (!stepData) {
		return;
	}
	let steps = getPrintableStepData(stepData, stepsList);

	return (
		<div className="sn-catalog-wizard-review-and-submit">
			{preview ? (
				''
			) : (
				<div className="sn-catalog-wizard-review-and-submit-step-name">
					<span>{REVIEW_AND_SUBMIT}</span>
				</div>
			)}
			{preview ? '' : getDescription(submitButtonLabel, submitting)}
			{getAccordionView(steps, stepsList)}
			{preview ? '' : getDescription(submitButtonLabel, submitting)}
		</div>
	);
};

const getPrintableStepData = (stepData, stepsList) => {
	let steps = cloneDeep(stepData);
	steps.forEach((step, index) => {
		if (!stepsList[index].hidden && !step.isCustom) {
			step.sections.forEach((section) => {
				if (section.visible) {
					Object.keys(section.questions).forEach((question) => {
						section.questions[question].readonly = true;
						section.questions[question].templateReadonly = false;
						section.questions[question].mandatory = false;
						section.questions[question].isInvalid = false;
						section.questions[question].messages = [];
					});
				}
			});
		}
	});
	return steps;
};
const getDescription = (submitButtonLabel, submitting) => {
	return (
		<div className="sn-catalog-wizard-review-and-submit-description">
			<span>{CATALOG_WIZARD_REVIEW_DESC}</span>
			<now-button
				disabled={submitting}
				label={submitButtonLabel}
				variant="primary"
				size="md"></now-button>
		</div>
	);
};

const getAccordionView = (stepData, stepsList) => {
	return (
		<now-accordion hide-dividers="true" heading-level="1">
			{getAccordionItems(stepData, stepsList)}
		</now-accordion>
	);
};

//TODO: What to show for a custom step is to be decided by the custom macroponent. Today we are rendering nothing if sections are empty.
//TODO: Pass readonly as a property to the custom component and let it decide what to show.
const getAccordionItems = (steps, stepsList) => {
	return (
		<div>
			{steps.map((step, stepIndex) => {
				if (
					stepsList[stepIndex].hidden ||
					!step.sections ||
					step.sections.length < 1
				) {
					return;
				}

				return (
					<div className="sn-catalog-wizard-review-and-submit-step-details">
						<now-accordion-item
							header={{
								label: `${get(step, 'name')}`,
								weight: 'bold',
								size: 'lg',
								variant: 'primary',
							}}
							trigger-icon={{
								type: 'chevron',
								position: 'start',
							}}
							expanded>
							<div
								className="sn-catalog-wizard-review-and-submit-header-divider"
								slot="content">
								<hr></hr>
							</div>

							<div slot="content">
								{step.sections.map((section, sectionId) => {
									if (!section.visible) {
										return;
									}
									return (
										<div className="sn-catalog-wizard-review-and-submit-section">
											<sn-catalog-wizard-step-section
												section-data={section}
												section-id={sectionId}
												section-style="flat"
												step-id={stepIndex}
												read-only-mode="printable"
											/>
										</div>
									);
								})}
							</div>
						</now-accordion-item>
					</div>
				);
			})}
		</div>
	);
};

export default view;
