import {cloneDeep, isEmpty} from 'lodash';
import {
	mergeFieldsWithWizard,
	updateStepSectionVisibility,
	resolveDynamicTitle,
	updateMacroVariables,
	updateAttachmentVariables,
} from './parseWizardData';
import '@servicenow/now-loader';
import {ERROR_FETCHING_RESOURCE, WIZARD_TITLE_CHANGED} from './constants';
import '@servicenow/now-alert';

const getNotifications = (notificationItems) => {
	if (isEmpty(notificationItems)) {
		return;
	}
	return (
		<now-alert-list
			configAriaLive="polite"
			items={notificationItems}></now-alert-list>
	);
};
export const view = (state, {dispatch}) => {
	const {
		wizardMeta,
		notificationItems,
		macroVariableUpdateProperties = [],
		attachmentContentTypes = {},
	} = state;
	const {
		properties: {
			variables = {},
			isLoading,
			isError,
			submitting,
			formData,
			formDirty,
			mode,
			wizardData,
			dataFetchOnly,
			wizardTitle: wizardTitleFromProps,
			hideTitle = false,
			hidePreviewButton = false,
		},
	} = state;

	if (isLoading || dataFetchOnly) {
		return;
	}
	if (isError) {
		return (
			<div>
				<now-alert
					status="critical"
					icon="exclamation-triangle-outline"
					header=""
					content={ERROR_FETCHING_RESOURCE}></now-alert>
			</div>
		);
	}
	updateMacroVariables(
		wizardData.macroVariables,
		variables,
		state.properties,
		macroVariableUpdateProperties
	);
	updateAttachmentVariables(variables, attachmentContentTypes);
	mergeFieldsWithWizard(wizardData, variables);
	updateStepSectionVisibility(wizardData);
	if (wizardMeta && wizardData) {
		const newTitle = resolveDynamicTitle({wizardData, variables, mode});
		if (!wizardTitleFromProps && wizardMeta.wizardTitle !== newTitle) {
			dispatch(WIZARD_TITLE_CHANGED, {
				value: newTitle,
			});
		}
		wizardMeta.wizardTitle = newTitle;
	}
	const {steps, stepsList, sysId} = wizardData;
	return (
		<div>
			{getNotifications(notificationItems)}
			<sn-catalog-wizard
				steps={cloneDeep(steps)}
				stepsList={cloneDeep(stepsList)}
				wizardMeta={wizardMeta}
				sysId={sysId}
				submitting={submitting}
				formData={formData}
				formDirty={formDirty}
				mode={mode}
				hideTitle={hideTitle}
				hidePreviewButton={hidePreviewButton}
				wizardTitle={wizardTitleFromProps}
			/>
		</div>
	);
};
