import '@servicenow/now-image';
import {createField, createLayout} from './util';
import '@servicenow/now-rich-text';
import '@servicenow/now-icon';
import {
	WIZARD_FEEDBACK_PANEL_TOGGLED,
	CHEVRON_LEFT_FILL,
	CHEVRON_RIGHT_FILL,
	SHOW_SECTION_INFO,
	HIDE_SECTION_INFO,
	STACKED,
} from '../constants';

const view = (state, helpers) => {
	const {
		properties: {
			feedback = {},
			wizardData: {layout},
		},
		collapsed,
	} = state;

	let feedbackAndPreviewClassName =
		layout == STACKED
			? collapsed
				? 'sn-catalog-wizard-feedback-and-preview-collapsed'
				: 'sn-catalog-wizard-feedback-and-preview-stacked'
			: 'sn-catalog-wizard-feedback-and-preview-tabbed';
	return (
		<div className={feedbackAndPreviewClassName}>
			{collapsed
				? renderToggleIcon(state, helpers)
				: !feedback.isCustom
				? getFeedback(state, helpers)
				: renderCustomFeedback(state, helpers)}
		</div>
	);
};

const getFeedback = (state, helpers) => {
	const {
		properties: {
			feedback = {},
			wizardData: {layout},
		},
	} = state;
	return (
		<div className="sn-catalog-wizard-feedback">
			<div className="sn-catalog-wizard-feedback-title">
				<span className="sn-catalog-wizard-feedback-title-name">
					{feedback.title}
				</span>
				{layout == STACKED ? renderToggleIcon(state, helpers) : null}
			</div>
			<div className="sn-catalog-wizard-feedback-content">
				<now-rich-text html={feedback.content}></now-rich-text>
			</div>
		</div>
	);
};

const renderCustomFeedback = (state, helpers) => {
	const {
		properties: {
			feedback = {},
			formData,
			wizardData: {layout},
		},
	} = state;
	return (
		<div className="sn-catalog-wizard-feedback">
			<div className="sn-catalog-wizard-feedback-title">
				<span className="sn-catalog-wizard-feedback-title-name-custom">
					{feedback.title}
				</span>
				{layout == STACKED ? renderToggleIcon(state, helpers) : null}
			</div>
			<sn-catalog-form
				source-table="sc_wizard_feedback"
				source-id={feedback.sysId}
				fields={createField(feedback, formData)}
				variables-layout={createLayout(feedback)}
				no-gutter="true"
			/>
		</div>
	);
};

const renderToggleIcon = (state, {dispatch}) => {
	const {collapsed} = state;
	let iconName = collapsed ? CHEVRON_LEFT_FILL : CHEVRON_RIGHT_FILL;
	let tooltipValue = collapsed ? SHOW_SECTION_INFO : HIDE_SECTION_INFO;
	return (
		<now-button-iconic
			icon={iconName}
			size="lg"
			on-click={() => {
				dispatch(WIZARD_FEEDBACK_PANEL_TOGGLED, {
					collapsed: !collapsed,
				});
			}}
			hidePadding={true}
			tooltipContent={tooltipValue}
			variant="tertiary"
			configAria={{button: {'aria-label': tooltipValue}}}></now-button-iconic>
	);
};

export default view;
