import {isEmpty, filter, get} from 'lodash';
import {t} from 'sn-translate';
import {
	CHOICE,
	MULTI_SELECT,
	ATTACHMENT,
	VARIABLES_PREFIX,
	SC_CAT_ITEM,
	SC_ATTACHMENT,
	SYSPARM_TEMPLATE_LIST,
	MACRO,
} from './constants';

export const parseWizardData = (wizard, stepsArray, mode) => {
	let steps = wizard.steps;
	const macroVariables = [];
	steps.forEach((step, index) => {
		if (!step.isCustom) {
			let stepHidden = true;
			step.sections.forEach((section) => {
				let sectionVisible = false;
				if (section.questions) {
					const variablesLayout = [];
					section.questions = section.questions.reduce(
						(questions, question) => {
							transformVariable(question, wizard, mode);
							let type = question.type == MULTI_SELECT ? MULTI_SELECT : 'field';
							variablesLayout.push({
								name: question.name,
								type: type,
								parent: '',
							});
							if (
								question.mandatory &&
								question.visible &&
								!stepsArray[index].isMandatory
							) {
								stepsArray[index].isMandatory = true;
								stepsArray[index].icon = 'asterisk-outline';
							}

							if (isQuestionVisible(question) && !sectionVisible) {
								sectionVisible = true;
								if (stepHidden) {
									stepHidden = false;
								}
							}
							if (question.type == MACRO) {
								macroVariables.push(question.name);
							}
							return {
								...questions,
								[question.name]: question,
							};
						},
						{}
					);
					section.variablesLayout = variablesLayout;
					section.visible = sectionVisible;
				}
			});
			stepsArray[index].hidden = stepHidden;
		}
	});
	wizard.macroVariables = macroVariables;
};

const transformVariable = (field, wizard, mode) => {
	field.readonly =
		field.readonly || field.templateReadonly || field.sys_readonly;
	field.mandatory = field.mandatory || field.templateMandatory;
	if (field.type == CHOICE) {
		field.choices.forEach((choice) => {
			choice.displayValue = choice.label;
		});
	}
	if (field.type == MULTI_SELECT) {
		field.fields[0].type = field.fields[0].internalType.toString();
	}
	if (field.type == SC_ATTACHMENT) {
		field.type = ATTACHMENT;
	}
	field.variable_name = field.name;

	if (SYSPARM_TEMPLATE_LIST.indexOf(field.variableName) == -1) {
		field.name = VARIABLES_PREFIX + field.name;
		field.referringTable = SC_CAT_ITEM;
	} else {
		field.referringTable = field.reference;
	}
	field.referringRecordId = wizard.producer;
	field.variableAttributes = field.attributes || '';
	field.targetRecordId = wizard.targetRecordData.targetRecord;
	field.targetRecordTableName = wizard.targetTable;
	delete field.attributes;
	const attributes = [];
	get(field, 'variableAttributes', '')
		.split(',')
		.forEach((attr = '') => {
			const parts = attr.split('=', 2);
			if (parts.length > 0) {
				const value = parts.length === 2 ? parts[1] : '';
				attributes.push({name: parts[0], value});
			}
		});
	field.dictionary = {
		attributes,
	};
	if (
		field.variableAttributes &&
		field.variableAttributes.indexOf('ref_qual_elements=') != -1
	) {
		let refQualElementAttribute = field.variableAttributes
			.split(',')
			.find((attr) => attr.startsWith('ref_qual_elements='));
		field.dependentField =
			VARIABLES_PREFIX + refQualElementAttribute.split('=')[1].split(';')[0];
	}
	if (field.type == MACRO) {
		field.mode = mode;
	}
};

export const updateMacroVariables = (
	macroVariables = [],
	variables,
	properties,
	macroVarUpdateProps = []
) => {
	macroVariables.forEach((m) => {
		macroVarUpdateProps.forEach((prop) => {
			variables[m][prop] = properties[prop];
		});
	});
};

export const updateAttachmentVariables = (
	variables,
	attachmentContentTypes
) => {
	Object.keys(attachmentContentTypes).forEach(
		(av) => (variables[av].contentType = attachmentContentTypes[av])
	);
};

export const mergeFieldsWithWizard = (wizard, fields) => {
	let steps = wizard.steps;
	if (wizard.steps && !isEmpty(fields)) {
		steps.forEach((step) => {
			if (!step.isCustom && step.sections) {
				step.sections.forEach((section) => {
					section.questions = Object.keys(section.questions).reduce(
						(acc, key) => {
							acc[key] = fields[key];
							return acc;
						},
						{}
					);
				});
			}
		});
	}
};

export const updateStepSectionVisibility = (wizard) => {
	let steps = wizard.steps;
	let stepsArray = wizard.stepsList;
	if (wizard.steps) {
		steps.forEach((step, index) => {
			if (!step.isCustom) {
				step.sections.forEach((section) => {
					section.visible = !isEmpty(
						filter(section.questions, (question) =>
							question ? question.visible : false
						)
					);
				});
				wizard.stepsList[index].hidden = isEmpty(
					filter(step.sections, (section) => section.visible)
				);
				let stepMandatory = false;
				let stepError = false;
				step.sections.forEach((section) => {
					if (!stepError) {
						stepError = !isEmpty(
							filter(
								section.questions,
								(question) => isQuestionVisible(question) && question.isInvalid
							)
						);
					}
					if (!stepMandatory) {
						stepMandatory = !isEmpty(
							filter(
								section.questions,
								(question) => isQuestionVisible(question) && question.mandatory
							)
						);
					}
				});
				stepsArray[index].isMandatory = stepMandatory;
				if (stepError) {
					stepsArray[index].icon = 'circle-fill';
					stepsArray[index].iconColor = 'has-error';
				} else if (stepMandatory) {
					stepsArray[index].icon = 'circle-fill';
					stepsArray[index].iconColor = 'has-empty-mandatory';
				} else {
					stepsArray[index].icon = '';
					stepsArray[index].iconColor = '';
				}
			}
		});
	}
};

const booleanValue = (value) => {
	value = value ? value + '' : '';
	return value === 'true';
};

const isQuestionVisible = (question) => {
	if (question) {
		return !('visible' in question && !booleanValue(question.visible));
	}
	return false;
};

export const resolveDynamicTitle = ({
	wizardData: {dynamicTitle = '', name = ''},
	variables,
	mode,
}) => {
	if (mode === 'template') {
		let templateName = variables[
			'variables.sysparm_template_name'
		].value.trim();
		const templateText = t('Template');
		return templateName ? `${templateText}: ${templateName}` : templateText;
	}

	var resolvedTitle = '';
	dynamicTitle.split(' ').forEach((item) => {
		if (item.startsWith('%') && variables['variables.' + item.substring(1)]) {
			resolvedTitle +=
				variables['variables.' + item.substring(1)].displayValue + ' ';
		} else {
			resolvedTitle += item + ' ';
		}
	});

	return resolvedTitle.trim() ? resolvedTitle.trim() : name;
};
