import {
	FETCH_SESSION_MESSAGES_REQUESTED,
	RENDER_NOTIFICATIONS_REQUESTED,
	FETCH_SESSION_MESSAGES_FAILED,
	NEW_MESSAGE_ARRIVED,
	MESSAGE_TYPES,
	FETCH_USER_DATA_FAILED,
	FETCH_USER_DATA_SUCCEEDED,
	FETCH_USER_DATA_REQUESTED,
	NOW_UXF_PAGE_ADD_NOTIFICATIONS,
} from '../constants';
import {catalogWizardMessages} from '../../../messages';
import {fetchSessionMessages, fetchUserData} from '../interceptors';
import {cloneDeep, get, isEmpty} from 'lodash';

export default {
	[FETCH_USER_DATA_REQUESTED]: {
		stopPropagation: true,
		interceptors: [fetchUserData],
	},

	[FETCH_USER_DATA_SUCCEEDED]: ({
		action,
		dispatch,
		properties,
		updateProperties,
	}) => {
		if (action.payload.errors && action.payload.errors.length > 0) {
			dispatch(FETCH_USER_DATA_FAILED);
			return;
		}
		const data = get(action, 'payload.data');
		window.ux_globals.presource['sn-workspace-header:wsUserData'] = {
			data,
		};
		const {variables} = properties;
		if (!isEmpty(variables)) {
			//If the user data arrives late re-render
			updateProperties({
				variables: cloneDeep(variables),
			});
		}
	},

	[FETCH_SESSION_MESSAGES_REQUESTED]: {
		stopPropagation: true,
		interceptors: [fetchSessionMessages],
	},

	[RENDER_NOTIFICATIONS_REQUESTED]: ({dispatch, action}) => {
		action.stopPropagation();
		if (action.payload.errors && action.payload.errors.length > 0) {
			dispatch(FETCH_SESSION_MESSAGES_FAILED);
			return;
		}
		const notifications = get(
			action,
			'payload.data.GlideDomain_Query.session.notifications',
			[]
		);
		notifications.forEach((element) => {
			let {type, text} = element;
			if (type == 'info' || type == 'error') {
				const payload = {
					id: Math.random(),
					action: {
						type: 'dismiss',
					},
					content: {
						type: 'html',
						value: text,
					},
					status: type == 'info' ? type : 'critical',
					expanded: true,
				};
				dispatch(NOW_UXF_PAGE_ADD_NOTIFICATIONS, {
					items: [payload],
				});
			}
		});
	},

	[FETCH_SESSION_MESSAGES_FAILED]: ({action, dispatch}) => {
		action.stopPropagation();
		const {payload = {}} = action;
		let message;
		if (payload.data.error) {
			message = payload.data.error.message;
		} else {
			message = catalogWizardMessages.ERROR_OCCURED;
		}
		dispatch(NEW_MESSAGE_ARRIVED, {
			type: MESSAGE_TYPES.ERROR,
			message,
		});
	},
};
