import '@servicenow/now-icon';
import {getStepSections} from './stepSections';
import '@servicenow/now-button';
import {STACKED, TABBED, WIZARD_STEP_SELECTED} from '../constants';
import {t} from 'sn-translate';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import _ from 'lodash';

const view = (state) => {
	const {properties} = state;
	const {stepDetails, nextStep, layout, wizardSysId, formData} = properties;
	const {stepIndex, stepData} = stepDetails;

	let wizardData = {sysId: wizardSysId, layout};
	let stepSectionsClass = 'sn-catalog-wizard-step-sections-';
	stepSectionsClass += layout == STACKED ? 'stacked' : 'tabbed';
	let detailsClassName =
		layout == STACKED
			? 'sn-catalog-wizard-step-details-stacked'
			: 'sn-catalog-wizard-step-details-tabbed';
	if (layout === TABBED && stepData.sections == 0) {
		stepSectionsClass = stepSectionsClass + ' ' + 'hide-elements';
	}
	let stepActionClassName =
		layout == STACKED
			? 'sn-catalog-wizard-step-action-stacked'
			: 'sn-catalog-wizard-step-action-tabbed';
	return (
		<div className={detailsClassName}>
			{getStepDetailName(stepData, layout)}
			<div className={stepSectionsClass}>
				{_.isEmpty(stepData.sections)
					? null
					: getStepSections(stepData.sections, stepIndex, wizardData, formData)}
			</div>
			<div className={stepActionClassName}>
				{nextStep ? (
					<div>
						<now-button-bare
							className={
								'wizard-btn ' +
								(layout === STACKED
									? 'btn-continue-stacked'
									: 'btn-continue-tabbed')
							}
							has-padding="false"
							icon-end="arrow-right-outline"
							variant="secondary"
							size="sm"
							label={t('Continue to {0}', nextStep.name)}
							append-to-payload={{
								action: WIZARD_STEP_SELECTED,
								value: nextStep.index,
							}}
						/>
					</div>
				) : null}
			</div>
		</div>
	);
};

const getStepDetailName = (stepData, layout) => {
	if (layout === STACKED) {
		return (
			<Fragment>
				<div className="sn-catalog-wizard-step-name">
					<span>{stepData.name}</span>
				</div>
				<div className="sn-catalog-wizard-step-short-description">
					<span>{stepData.shortDescription}</span>
				</div>
			</Fragment>
		);
	}
};

export default view;
