import {
	SUBMIT_CLICKED,
	SUBMIT_VALIDATED,
	SUBMITTED,
	SUBMIT_FAILED,
	REQUEST_GENERATED,
	NEW_MESSAGE_ARRIVED,
	MESSAGE_TYPES,
	SAVE_CLICKED,
	ACTION_TYPES,
	SAVED,
	SUBMIT_WIZARD_VALIDATED,
	WIZARD_ROUTE_PARAMS_CHANGED,
	FETCH_SESSION_MESSAGES_REQUESTED,
	NOW_UXF_PAGE_SET_LOADING_STATE,
	SUBMITTING,
} from '../constants';
import {catalogWizardMessages} from '../../../messages';
import {createBeforeSubmitInterceptor} from '../interceptors';
import {isAESTab, updateAESTabDirty} from '../utils';

export default {
	[SUBMIT_CLICKED]: ({state}) => {
		const {gForm} = state;
		gForm && gForm.submit();
	},

	[SAVE_CLICKED]: ({dispatch, properties: {formDirty, submitting}, action}) => {
		action.stopPropagation();
		if (formDirty && !submitting) {
			dispatch(SUBMIT_VALIDATED, {actionType: ACTION_TYPES.SAVE});
		}
	},

	[SUBMIT_WIZARD_VALIDATED]: {
		stopPropagation: true,
		interceptors: [createBeforeSubmitInterceptor],
	},

	[SUBMIT_VALIDATED]: ({
		dispatch,
		properties,
		updateProperties,
		action: {payload = {}, stopPropagation},
	}) => {
		stopPropagation();
		const {actionType = ACTION_TYPES.SUBMIT} = payload;
		dispatch(NOW_UXF_PAGE_SET_LOADING_STATE, {
			id: 'non_blocking',
			loading: true,
			nonBlocking: ACTION_TYPES.SAVE === actionType,
			label: ACTION_TYPES.SUBMIT === actionType ? SUBMITTING : '',
		});
		let {mode, submitting} = properties;
		if (submitting) {
			return;
		}
		dispatch(SUBMIT_WIZARD_VALIDATED, {actionType, mode});
		updateProperties({
			submitting: true,
		});
	},

	[SUBMITTED]: ({dispatch, action: {payload = {}}, updateProperties}) => {
		const {result = {}} = payload;
		updateProperties({
			submitting: false,
			formDirty: false,
			editMode: true,
		});
		// update AES tab
		if (isAESTab()) {
			updateAESTabDirty(false);
		}

		dispatch(NOW_UXF_PAGE_SET_LOADING_STATE, {
			id: 'blocking',
			loading: false,
		});
		dispatch(REQUEST_GENERATED, {
			table: result.table,
			sys_id: result.sys_id,
		});
		dispatch(FETCH_SESSION_MESSAGES_REQUESTED);
	},

	[SAVED]: ({updateProperties, action, dispatch}) => {
		action.stopPropagation();
		updateProperties({
			submitting: false,
			formDirty: false,
			editMode: true,
		});
		// update AES tab
		if (isAESTab()) {
			updateAESTabDirty(false);
		}

		const {result = {}} = action.payload;
		const {sys_id = ''} = result;
		dispatch(WIZARD_ROUTE_PARAMS_CHANGED, {
			recordId: sys_id,
		});
		dispatch(FETCH_SESSION_MESSAGES_REQUESTED);
		dispatch(NOW_UXF_PAGE_SET_LOADING_STATE, {
			id: 'non_blocking',
			loading: false,
		});
	},

	[SUBMIT_FAILED]: ({action, dispatch, updateProperties}) => {
		updateProperties({
			submitting: false,
		});
		const {payload = {}} = action;
		let message;
		if (payload.data.error) {
			message = payload.data.error.message;
		} else if (payload.data.result) {
			message = payload.data.result.errMsg;
		} else {
			message = catalogWizardMessages.ERROR_OCCURED;
		}
		dispatch(NOW_UXF_PAGE_SET_LOADING_STATE, {
			id: 'blocking',
			loading: false,
		});
		dispatch(NEW_MESSAGE_ARRIVED, {
			type: MESSAGE_TYPES.ERROR,
			message,
		});
		dispatch(FETCH_SESSION_MESSAGES_REQUESTED);
	},
};
