// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import './wizard-steps';
import './wizard-step-details';
import './wizard-feedback-and-preview';
import '@servicenow/now-tabs';
import '@servicenow/now-button';
import '@servicenow/now-alert';
import '@servicenow/now-modal';
import {
	STACKED,
	TABBED,
	WIZARD_CANCEL_CLICKED,
	WIZARD_SAVE_CLICKED,
	WIZARD_SUBMIT_CLICKED,
	WIZARD_PREVIEW_CLICKED,
	TEMPLATE_MODE,
	WIZARD_HOME_CLICKED,
	WIZARD_HOME_ALERT_MODAL_CANCEL_ID,
	WIZARD_HOME_ALERT_MODAL_CONTINUE_ID,
	WIZARD_HOMEPAGE_MODAL_FOOTER_CLICKED,
	WIZARD_SETTINGS_ITEM_CLICKED,
	WIZARD_DELETE_MODAL_FOOTER_CLICKED,
	DELETE_MODAL_CANCEL_ID,
	DELETE_MODAL_CONFIRM_ID,
	DELETE_ID,
} from './constants';
import {catalogWizardMessages} from '../../messages';
import './../sn-catalog-wizard-review';
import './../sn-catalog-builder-item-preview';

const view = (state) => {
	const {properties, isError, message} = state;
	const {layout} = properties.wizardMeta;
	if (isError) {
		return (
			<div className="wizard-display-error">
				<now-alert status="critical" content={message}></now-alert>
			</div>
		);
	}
	return (
		<div className="sn-catalog-wizard">
			{layout === TABBED
				? getWizardTabbedView(state)
				: getWizardStackedView(state)}
		</div>
	);
};

const getWizardStackedView = ({
	currentSelectedStep,
	nextStep,
	showPreview,
	properties: {
		stepsList,
		steps,
		sysId,
		submitting,
		formData,
		formDirty,
		mode,
		hideTitle = false,
		wizardTitle: wizardTitleFromProps,
		hidePreviewButton,
		wizardMeta: {
			wizardTitle: wizardTitleFromMeta,
			layout,
			picture,
			hasFeedbackPanel = true,
			showSave,
			showSubmit,
			showCancel,
			targetRecordData,
			submitButtonLabel = catalogWizardMessages.SUBMIT,
			previewPortal,
			hideHomeButton,
		},
	},
	homePageAlertOpened,
	showDeleteModal,
}) => {
	const stepData = steps[currentSelectedStep];
	const wizardData = {wizardId: sysId, layout, mode};
	if (!stepData) {
		return;
	}
	const wizardTitle = wizardTitleFromProps || wizardTitleFromMeta;
	return (
		<Fragment>
			{getWizardTitle(
				wizardTitle,
				'',
				layout,
				picture,
				{
					showCancel,
					showSave,
					showSubmit,
					submitting,
					formDirty,
					showPreviewButton: !hidePreviewButton && mode !== TEMPLATE_MODE,
					submitButtonLabel,
					showSettings: showSettingsButton({mode, targetRecordData}),
					showTitle: !hideTitle,
				},
				showPreview,
				targetRecordData.targetRecord,
				previewPortal,
				hideHomeButton,
				homePageAlertOpened,
				showDeleteModal
			)}
			<div className="sn-catalog-wizard-stacked">
				{getWizardSteps(layout, stepsList, currentSelectedStep)}
				{stepData.isCustom
					? getCustomStepDetails(
							layout,
							submitting,
							steps,
							stepData.macroponent,
							stepsList,
							submitButtonLabel
					  )
					: getStepDetails(
							sysId,
							{stepIndex: currentSelectedStep, stepData},
							layout,
							nextStep,
							stepsList,
							formData
					  )}
				{hasFeedbackPanel
					? getWizardFeedback(stepData, steps, formData, wizardData)
					: null}
			</div>
		</Fragment>
	);
};

const getWizardTabbedView = ({
	currentSelectedStep,
	nextStep,
	properties: {
		sysId,
		stepsList,
		steps,
		submitting,
		formData,
		formDirty,
		hideTitle = false,
		wizardMeta: {
			wizardTitle,
			wizardShortDescription,
			hasFeedbackPanel = true,
			layout,
			showSave,
			showSubmit,
			showCancel,
			submitButtonLabel = catalogWizardMessages.SUBMIT,
		},
	},
}) => {
	const stepData = steps[currentSelectedStep];
	const wizardData = {wizardId: sysId, layout};
	if (!stepData) {
		return;
	}
	return (
		<Fragment>
			{getWizardTitle(wizardTitle, wizardShortDescription, layout, '', {
				showTitle: !hideTitle,
			})}
			<div className="sn-catalog-wizard-tabbed">
				<div className="sn-catalog-wizard-tabbed-steps-and-feedback">
					<div className="sn-catalog-wizard-tabbed-steps-and-details">
						{getWizardSteps(layout, stepsList, currentSelectedStep)}
						{stepData.isCustom
							? getCustomStepDetails(
									layout,
									submitting,
									steps,
									stepData.macroponent,
									stepsList,
									submitButtonLabel
							  )
							: getStepDetails(
									sysId,
									{stepIndex: currentSelectedStep, stepData},
									layout,
									nextStep,
									stepsList,
									formData
							  )}
					</div>
					{hasFeedbackPanel
						? getWizardFeedback(stepData, steps, formData, wizardData)
						: null}
				</div>
				{getBottomActionBar({
					showCancel,
					showSave,
					showSubmit,
					submitting,
					formDirty,
					submitButtonLabel,
				})}
			</div>
		</Fragment>
	);
};

const getBottomActionBar = (wizardActions) => {
	return (
		<div className="sn-catalog-wizard-tabbed-action-bar">
			<div className="sn-catalog-wizard-tabbed-now-actions">
				<div className="sn-catalog-wizard-tabbed-now-action-cancle-div">
					{wizardActions.showCancel ? (
						<now-button
							label={catalogWizardMessages.CANCEL}
							tooltipContent={catalogWizardMessages.CANCEL}
							icon=""
							variant="tertiary"
							append-to-payload={{action: WIZARD_CANCEL_CLICKED}}
							size="md"></now-button>
					) : null}
				</div>
				<div className="sn-catalog-wizard-tabbed-now-action-save-div">
					{wizardActions.showSave ? (
						<now-button
							label={catalogWizardMessages.SAVE}
							tooltipContent={catalogWizardMessages.SAVE}
							variant={wizardActions.showSubmit ? 'secondary' : 'primary'}
							disabled={!wizardActions.formDirty || wizardActions.submitting}
							append-to-payload={{action: WIZARD_SAVE_CLICKED}}
							size="md"></now-button>
					) : null}
				</div>
				<div className="sn-catalog-wizard-tabbed-now-action-submit-div">
					{wizardActions.showSubmit ? (
						<now-button
							label={wizardActions.submitButtonLabel}
							tooltipContent={wizardActions.submitButtonLabel}
							variant="primary"
							disabled={wizardActions.submitting}
							append-to-payload={{action: WIZARD_SUBMIT_CLICKED}}
							size="md"></now-button>
					) : null}
				</div>
			</div>
		</div>
	);
};

const getWizardTitle = (
	title,
	shortDescription,
	layout,
	picture,
	wizardActions = {},
	showPreview,
	targetRecord,
	previewPortal,
	hideHomeButton,
	homePageAlertOpened,
	showDeleteModal
) => {
	return layout === STACKED ? (
		<div className="sn-catalog-wizard-stacked-title-bar">
			{hideHomeButton ? null : getHomeButton(homePageAlertOpened)}
			{(() => {
				return picture ? (
					<div className="sn-catalog-wizard-stacked-picture">
						<now-image
							src={picture + '?t=small'}
							alt={title}
							width="32"
							height="32"></now-image>
					</div>
				) : null;
			})()}
			{wizardActions.showTitle ? (
				<div className="sn-catalog-wizard-stacked-title">{title}</div>
			) : null}
			<div className="sn-catalog-wizard-stacked-actions">
				{wizardActions.showPreviewButton ? (
					<div className="sn-catalog-wizard-stacked-preview-btn">
						<now-button
							label={catalogWizardMessages.PREVIEW}
							tooltipContent={catalogWizardMessages.PREVIEW}
							variant="secondary"
							size="md"
							append-to-payload={{action: WIZARD_PREVIEW_CLICKED}}></now-button>
					</div>
				) : null}
				{showPreview
					? getPreviewModal(showPreview, targetRecord, previewPortal)
					: null}
				{wizardActions.showSave ? (
					<div className="sn-catalog-wizard-stacked-save-btn">
						<now-button
							label={catalogWizardMessages.SAVE}
							tooltipContent={catalogWizardMessages.SAVE}
							variant={wizardActions.showSubmit ? 'secondary' : 'primary'}
							disabled={!wizardActions.formDirty || wizardActions.submitting}
							append-to-payload={{action: WIZARD_SAVE_CLICKED}}
							size="md"></now-button>
					</div>
				) : null}
				{wizardActions.showSubmit ? (
					<div className="sn-catalog-wizard-stacked-submit-btn">
						<now-button
							label={wizardActions.submitButtonLabel}
							tooltipContent={wizardActions.submitButtonLabel}
							variant="primary"
							disabled={wizardActions.submitting}
							append-to-payload={{action: WIZARD_SUBMIT_CLICKED}}
							size="md"></now-button>
					</div>
				) : null}
				{wizardActions.showSettings
					? getSettingsButton({showDeleteModal})
					: null}
			</div>
		</div>
	) : (
		<div className="sn-catalog-wizard-tabbed-title-bar">
			{wizardActions.showTitle ? (
				<div className="sn-catalog-wizard-tabbed-title">{title}</div>
			) : null}
			<div className="sn-catalog-wizard-tabbed-short-description">
				{shortDescription}
			</div>
		</div>
	);
};

const showSettingsButton = ({
	mode,
	targetRecordData: {state, publishedItem},
}) => {
	return mode != TEMPLATE_MODE && !publishedItem && state == 'draft';
};

const getSettingsButton = ({showDeleteModal}) => {
	return (
		<div className="sn-catalog-wizard-stacked-settings-btn">
			<now-dropdown
				items={[
					{
						id: DELETE_ID,
						label: catalogWizardMessages.DELETE,
					},
				]}
				icon="ellipsis-h-outline"
				size="md"
				variant="secondary-selected"
				select="none"
				hideCaret={true}
				tooltipContent={catalogWizardMessages.SETTINGS}
				append-to-payload={{
					action: WIZARD_SETTINGS_ITEM_CLICKED,
				}}></now-dropdown>
			<now-modal
				content={catalogWizardMessages.SETTINGS_DELETE_MODAL_CONTENT}
				footerActions={[
					{
						label: catalogWizardMessages.CONFIRM,
						id: DELETE_MODAL_CONFIRM_ID,
						variant: 'primary-negative',
					},
					{
						label: catalogWizardMessages.CANCEL,
						id: DELETE_MODAL_CANCEL_ID,
						variant: 'secondary',
					},
				]}
				headerLabel={catalogWizardMessages.SETTINGS_DELETE_MODAL_HEADER}
				headerLevel="2"
				opened={showDeleteModal}
				size="md"
				append-to-payload={{
					action: WIZARD_DELETE_MODAL_FOOTER_CLICKED,
				}}></now-modal>
		</div>
	);
};

const getStepDetails = (
	sysId,
	stepDetails,
	layout,
	nextStep,
	stepsList,
	formData
) => {
	let stepDetailsClassName =
		layout === STACKED
			? 'sn-catalog-wizard-stacked-step-details'
			: 'sn-catalog-wizard-tabbed-step-details';
	{
		return stepDetails.stepData ? (
			<div className={stepDetailsClassName}>
				<sn-catalog-wizard-step-details
					formData={formData}
					wizard-sys-id={sysId}
					layout={layout}
					step-details={stepDetails}
					next-step={
						nextStep
							? {index: nextStep, name: stepsList[nextStep].label}
							: undefined
					}
				/>
			</div>
		) : null;
	}
};

const getCustomStepDetails = (
	layout,
	submitting,
	steps,
	Control,
	stepsList,
	submitButtonLabel
) => {
	let customStepClassName =
		layout === STACKED
			? 'sn-catalog-wizard-custom-step-details-stacked'
			: 'sn-catalog-wizard-custom-step-details-tabbed';
	return Control ? (
		<div className={customStepClassName}>
			<Control
				stepData={steps}
				stepsList={stepsList}
				submitting={submitting}
				submitButtonLabel={submitButtonLabel}
			/>
		</div>
	) : null;
};

const getWizardFeedback = (stepData, steps, formData, wizardData) => {
	const {layout} = wizardData;
	let feedbackClassName =
		layout === STACKED
			? 'sn-catalog-wizard-stacked-step-feedback'
			: 'sn-catalog-wizard-tabbed-step-feedback';
	{
		return stepData && stepData.feedback ? (
			<div className={feedbackClassName}>
				<sn-catalog-wizard-feedback-and-preview
					feedback={stepData.feedback}
					formData={formData}
					wizardData={wizardData}
				/>
			</div>
		) : null;
	}
};

const getWizardSteps = (layout, stepsList, currentSelectedStep) => {
	let stepsClassName =
		layout === STACKED
			? 'sn-catalog-wizard-stacked-steps'
			: 'sn-catalog-wizard-tabbed-steps';

	return (
		<div className={stepsClassName}>
			<sn-catalog-wizard-steps
				steps={stepsList}
				selected-step={currentSelectedStep}
				layout={layout}
			/>
		</div>
	);
};

const getPreviewModal = (showPreview, catalogItemSysId, previewPortal) => {
	return (
		<div className="sn-catalog-wizard-stacked-preview-modal">
			<now-modal
				opened={!!showPreview}
				header-label={catalogWizardMessages.PREVIEW_HEADING_LABEL}
				size="fullscreen">
				<sn-catalog-builder-item-preview
					sysId={catalogItemSysId}
					portal={previewPortal}
				/>
			</now-modal>
		</div>
	);
};

const getHomeButton = (homePageAlertOpened) => {
	return (
		<div className="sn-catalog-wizard-stacked-home">
			<div className="sn-catalog-wizard-stacked-home-btn">
				<now-button-iconic
					icon="home-outline"
					tooltipContent={catalogWizardMessages.RETURN_TO_DASHBOARD}
					size="lg"
					variant="primary"
					hide-padding="false"
					bare
					append-to-payload={{action: WIZARD_HOME_CLICKED}}></now-button-iconic>
			</div>
			<now-modal
				headerLabel={catalogWizardMessages.RETURN_TO_DASHBOARD}
				content={catalogWizardMessages.WIZARD_DIRTY_FORM_ALERT_MESSAGE}
				footerActions={[
					{
						id: WIZARD_HOME_ALERT_MODAL_CONTINUE_ID,
						label: catalogWizardMessages.CONTINUE,
						variant: 'primary-negative',
					},
					{
						id: WIZARD_HOME_ALERT_MODAL_CANCEL_ID,
						label: catalogWizardMessages.CANCEL,
						variant: 'secondary',
					},
				]}
				size="sm"
				opened={homePageAlertOpened}
				append-to-payload={{
					action: WIZARD_HOMEPAGE_MODAL_FOOTER_CLICKED,
				}}></now-modal>
		</div>
	);
};
export default view;
