import {actionTypes} from '@servicenow/ui-core';
import {
	NEW_MESSAGE_ARRIVED,
	MESSAGE_TYPES,
	SC_WIZARD,
	DOT,
	FEEDBACK_COLLAPSED,
	WIZARD_FEEDBACK_PANEL_PREFERENCE_REQUESTED,
	WIZARD_FEEDBACK_PANEL_PREFERENCE_FETCH_SUCCEEDED,
	WIZARD_FEEDBACK_PANEL_TOGGLED,
	WIZARD_FEEDBACK_PANEL_PREFERENCE_UPDATE_REQUESTED,
	WIZARD_FEEDBACK_PANEL_PREFERENCE_FETCH_OR_UPDATE_FAILED,
	STACKED,
} from '../constants';
import {
	USER_PREFERENCE_MUTATION_QUERY,
	USER_PREFERENCE_QUERY,
} from '../queryUtils';
import {catalogWizardMessages} from '../../../messages';
import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
const {COMPONENT_BOOTSTRAPPED} = actionTypes;

export default {
	[COMPONENT_BOOTSTRAPPED]: ({dispatch, properties}) => {
		const {
			wizardData: {wizardId, layout, mode},
		} = properties;
		if (layout == STACKED) {
			const feedbackPanelPreference =
				SC_WIZARD + DOT + wizardId + DOT + mode + DOT + FEEDBACK_COLLAPSED;
			dispatch(WIZARD_FEEDBACK_PANEL_PREFERENCE_REQUESTED, {
				name: feedbackPanelPreference,
			});
		}
	},

	[WIZARD_FEEDBACK_PANEL_PREFERENCE_REQUESTED]: createGraphQLEffect(
		USER_PREFERENCE_QUERY,
		{
			variableList: ['name'],
			successActionType: WIZARD_FEEDBACK_PANEL_PREFERENCE_FETCH_SUCCEEDED,
			errorActionType: WIZARD_FEEDBACK_PANEL_PREFERENCE_FETCH_OR_UPDATE_FAILED,
		}
	),

	[WIZARD_FEEDBACK_PANEL_PREFERENCE_FETCH_SUCCEEDED]: ({
		action,
		updateState,
	}) => {
		const {
			payload: {data},
		} = action;
		if (data && data.GlideDomain_Query) {
			let userTogglePreference =
				data.GlideDomain_Query.user.preferences[0].value === 'true';
			updateState({
				collapsed: userTogglePreference,
			});
		}
	},

	[WIZARD_FEEDBACK_PANEL_TOGGLED]: ({
		action: {
			payload: {collapsed},
		},
		updateState,
		dispatch,
		properties: {
			wizardData: {wizardId, mode},
		},
	}) => {
		const feedbackPanelPreference =
			SC_WIZARD + DOT + wizardId + DOT + mode + DOT + FEEDBACK_COLLAPSED;
		dispatch(WIZARD_FEEDBACK_PANEL_PREFERENCE_UPDATE_REQUESTED, {
			name: feedbackPanelPreference,
			value: collapsed,
		});
		updateState({
			collapsed: collapsed,
		});
	},

	[WIZARD_FEEDBACK_PANEL_PREFERENCE_UPDATE_REQUESTED]: createGraphQLEffect(
		USER_PREFERENCE_MUTATION_QUERY,
		{
			variableList: ['name', 'value'],
			errorActionType: WIZARD_FEEDBACK_PANEL_PREFERENCE_FETCH_OR_UPDATE_FAILED,
		}
	),

	[WIZARD_FEEDBACK_PANEL_PREFERENCE_FETCH_OR_UPDATE_FAILED]: ({
		action,
		dispatch,
	}) => {
		const {payload = {}} = action;
		let message;
		if (payload.errors) {
			message = payload.errors[0].message;
		} else {
			message = catalogWizardMessages.ERROR_OCCURED;
		}
		dispatch(NEW_MESSAGE_ARRIVED, {
			type: MESSAGE_TYPES.ERROR,
			message,
		});
	},
};
