import {
	PORTAL,
	MOBILE,
	PLATFORM,
	WORKSPACE,
	PLATFORM_URL,
	PORTAL_PAGE_URL,
	MOBILE_PAGE_URL,
} from './constants';
const transformState = (state) => {
	const {
		uiInterface,
		properties: {sysId, portal = 'swp'},
	} = state;
	let url;

	//TODO: url is the UIB page where the preview component is dragged
	switch (uiInterface) {
		case PORTAL:
			url = portal + PORTAL_PAGE_URL + sysId;
			break;
		case MOBILE:
			url = portal + MOBILE_PAGE_URL + sysId;
			break;
		case PLATFORM:
			url = PLATFORM_URL + sysId;
			break;
		case WORKSPACE:
			url = '';
			break;
	}

	return {
		...state,
		url,
	};
};

export default transformState;
