import ItemHandler from './itemHandler';
import {SYSPARM_TEMPLATE_LIST} from '../constants';

export default class TemplateItemHandler extends ItemHandler {
	getSubmitPayload(state = {}, properties = {}) {
		const {gForm} = state;
		const {
			targetRecord,
			sysId,
			mode,
			templateId,
			wizardDetails = {},
			parentRecordTable,
			parentRecordId,
		} = properties;
		const variables = (gForm.serialize() || []).reduce((acc, field) => {
			acc[field.variable_name] = field.value;
			return acc;
		}, {});
		const payload = {
			sysId: sysId,
			data: {
				variables,
				sysparm_template_type: wizardDetails.sysId,
				sysparm_parent_target_record_table: parentRecordTable,
				sysparm_parent_target_record_id: parentRecordId,
				...SYSPARM_TEMPLATE_LIST.reduce((parms, parm) => {
					if (variables[parm]) {
						parms[parm] = variables[parm];
					}
					return parms;
				}, {}),
			},
			recordId: targetRecord,
			mode: mode,
			templateId,
		};
		return payload;
	}
}
