export const USER_PREFERENCE_QUERY = `query($name: [String]!) {
    GlideDomain_Query {
	    user {
		    preferences(name: $name) {
		        name
                value
            }      
	    }
	}
}`;

export const USER_PREFERENCE_MUTATION_QUERY = `mutation ($name: String!,$value: String!){
	GlideDomain_Mutation {
	    user {
		    setPreference(name: $name,value: $value) {
				name
		        value
		    }
	    }
	}
}`;
