export const getNextStep = (steps, selectedStep) => {
	const filteredSteps = steps.filter((step) => !step.hidden);
	const selectedStepIndex = filteredSteps.findIndex(
		(step) => step.label === steps[selectedStep].label
	);
	if (selectedStepIndex >= 0 && filteredSteps[selectedStepIndex + 1]) {
		return filteredSteps[selectedStepIndex + 1].id;
	} else {
		if (steps[selectedStep].hidden) {
			for (var i = selectedStep + 1; i < steps.length; i++) {
				const nextStep = filteredSteps.findIndex(
					(step) => step.label === steps[i].label
				);
				if (nextStep >= 0) {
					return filteredSteps[nextStep].id;
				}
			}
			for (var j = selectedStep - 1; j >= 0; j--) {
				const prevStep = filteredSteps.findIndex(
					(step) => step.label === steps[j].label
				);
				if (prevStep >= 0) {
					return filteredSteps[prevStep].id;
				}
			}
		}
	}
};

export const _renderTooltip = (
	targetRef,
	componentId = '',
	tooltipContent,
	tooltipPosition = [],
	delay
) => {
	if (tooltipContent) {
		return (
			<now-tooltip
				id={componentId ? `tooltip-${componentId}` : `tooltip-0`}
				target-ref={targetRef}
				content={tooltipContent}
				position={tooltipPosition ? tooltipPosition : ['bottom-center top-end']}
				delay={delay ? delay : 200}
			/>
		);
	}
};
