import {
	SN_CATALOG_FORM_CHANGED,
	VARIABLES_UPDATED,
	MESSAGE_TYPES,
	NEW_MESSAGE_ARRIVED,
	SN_CATALOG_UI_MESSAGE_SET,
	SN_CATALOG_UI_MESSAGE_UNSET,
	SN_CATALOG_UI_MESSAGES_CLEARED,
	SN_CATALOG_FORM_CHANGED_DATA_FETCH_MODE,
	NOW_UXF_PAGE_ADD_NOTIFICATIONS,
	NOW_UXF_PAGE_NOTIFICATIONS_UPDATED,
	MULTI_SELECT,
	SAVE_CLICKED,
	UPDATE_ATTACHMENT_CONTENT_TYPE,
	ATTACHMENT,
	ATTACHMENT_DATA_GET_URL,
	ATTACHMENT_CONTENT_TYPE_FETCH_FAILED,
	ATTACHMENT_CONTENT_TYPE_FETCH_SUCCESS,
} from '../constants';
import {
	GFORM_ADD_ERROR_MESSAGE,
	GFORM_ADD_INFO_MESSAGE,
} from '../gFormActionNames';
import {cloneDeep} from 'lodash';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {isAESTab, updateAESTabDirty} from '../utils';

const isMultiSelectField = (field) => {
	const varAttrs = field.variableAttributes;
	if (varAttrs && varAttrs.length != 0) {
		let attrsArray = varAttrs.split(',');
		let multiSelect = attrsArray.find((attr) => attr.startsWith(MULTI_SELECT));
		return multiSelect;
	}
	return false;
};

export default {
	[VARIABLES_UPDATED]: ({
		action,
		dispatch,
		state,
		properties,
		updateProperties,
		updateState,
	}) => {
		const {formData, variables, dataFetchOnly} = properties;
		if (dataFetchOnly) {
			return;
		}
		const {payload = {}} = action;
		const {name, propertyName, value} = payload;
		const {gForm} = state;
		const changedVariable = variables[name];
		if (propertyName === 'value') {
			updateProperties({
				formData: {
					...formData,
					encodedRecord: gForm.getEncodedRecord(),
					serializedChanges: JSON.stringify(gForm.serialize()),
				},
				variables: {
					...variables,
					[name]: {
						...changedVariable,
						[propertyName]: value,
					},
				},
				formDirty: gForm ? gForm.isUserModified() : false,
			});
			// trigger save incase of multi select field value change
			if (isMultiSelectField(changedVariable)) {
				dispatch(SAVE_CLICKED);
			}
			if (changedVariable.type == ATTACHMENT) {
				updateState({
					path: `attachmentToVariableMap[${value}]`,
					operation: 'set',
					value: name,
					shouldRender: false,
				});
				dispatch(UPDATE_ATTACHMENT_CONTENT_TYPE, {sysId: value});
			}
			// If we are in AES, update the AES tab status as well
			if (isAESTab()) {
				updateAESTabDirty(gForm ? gForm.isUserModified() : false);
			}
		}
	},
	[NEW_MESSAGE_ARRIVED]: ({dispatch, action: {payload = {}}}) => {
		const {type = MESSAGE_TYPES.INFORMATION, message} = payload;
		let actionName = GFORM_ADD_INFO_MESSAGE;
		if (type === MESSAGE_TYPES.ERROR) {
			actionName = GFORM_ADD_ERROR_MESSAGE;
		}
		dispatch(actionName, {message});
	},
	[SN_CATALOG_FORM_CHANGED]: ({
		action,
		updateProperties,
		properties: {dataFetchOnly},
		dispatch,
	}) => {
		const {payload = {}} = action;
		const {fields} = payload;
		const variables = cloneDeep(fields);
		if (dataFetchOnly) {
			return dispatch(SN_CATALOG_FORM_CHANGED_DATA_FETCH_MODE, {variables});
		}
		updateProperties({
			variables,
		});
	},
	[SN_CATALOG_UI_MESSAGE_SET]: ({action, dispatch}) => {
		const {payload} = action;
		dispatch(NOW_UXF_PAGE_ADD_NOTIFICATIONS, {
			items: [
				{
					id: Math.random(),
					...payload,
					action: {
						type: 'dismiss',
					},
				},
			],
		});
	},
	[SN_CATALOG_UI_MESSAGE_UNSET]: ({action, updateState, state}) => {
		const {payload} = action;
		const {notificationItems = []} = state;
		let removeNotificationIndex = notificationItems.findIndex(
			(notification) => payload.alertId == notification.id
		);
		if (removeNotificationIndex >= 0) {
			updateState({
				path: 'notificationItems',
				operation: 'splice',
				start: removeNotificationIndex,
				deleteCount: 1,
			});
		}
	},
	[SN_CATALOG_UI_MESSAGES_CLEARED]: ({dispatch}) => {
		dispatch(NOW_UXF_PAGE_NOTIFICATIONS_UPDATED, {
			items: [],
		});
	},
	[UPDATE_ATTACHMENT_CONTENT_TYPE]: createHttpEffect(ATTACHMENT_DATA_GET_URL, {
		pathParams: ['sysId'],
		successActionType: ATTACHMENT_CONTENT_TYPE_FETCH_SUCCESS,
		errorActionType: ATTACHMENT_CONTENT_TYPE_FETCH_FAILED,
	}),
	[ATTACHMENT_CONTENT_TYPE_FETCH_SUCCESS]: ({
		state,
		action,
		updateState,
		dispatch,
	}) => {
		const {payload} = action;
		const {attachmentToVariableMap, attachmentContentTypes} = state;

		if (payload.errors && payload.errors.length > 0) {
			dispatch(ATTACHMENT_CONTENT_TYPE_FETCH_FAILED, payload.errors);
		}
		const {result = {}} = payload;
		if (result.sys_id && attachmentToVariableMap[result.sys_id]) {
			const attachVar = attachmentToVariableMap[result.sys_id];
			updateState({
				attachmentContentTypes: {
					...attachmentContentTypes,
					[attachVar]: result.content_type,
				},
			});
		}
	},
	[ATTACHMENT_CONTENT_TYPE_FETCH_FAILED]: ({action}) =>
		console.error('Failed to fetch attachment content type', action.payload),
};
