import {WIZARD_STACKED_STEP_CLICKED, STACKED} from '../constants';
import '../wizard-steps-label';
// eslint-disable-next-line no-unused-vars
import {Fragment} from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-tooltip';
import {_renderTooltip} from '../util';

const view = (state, {dispatch, updateState}) => {
	const {properties} = state;
	const {layout, selectedStep, steps} = properties;
	return (
		<div
			className={
				layout === STACKED
					? 'sn-catalog-wizard-steps-stacked'
					: 'sn-catalog-wizard-steps-tabbed'
			}>
			{layout == STACKED
				? getStackedSteps(selectedStep, steps, dispatch, updateState, state)
				: getTabbedSteps(
						selectedStep,
						steps.filter((step) => !step.hidden)
				  )}
		</div>
	);
};

const getTabbedSteps = (selectedStep, steps) => {
	return (
		<now-tabs
			className="sn-catalog-step-now-tabs"
			items={steps.map((s) => ({...s, label: getStepLabel(s), icon: null}))}
			selectedItem={selectedStep}></now-tabs>
	);
};

const getStepLabel = (step) => {
	return (
		<sn-catalog-wizard-steps-label
			icon={step.icon}
			color={step.iconColor}
			label={step.label}></sn-catalog-wizard-steps-label>
	);
};
const getStackedSteps = (selectedStep, steps, dispatch, updateState, state) => {
	return (
		<div className="sn-catalog-wizard-steps-stacked-list" role="tablist">
			{steps.map((step, index) => {
				const stepClass =
					step.id === selectedStep
						? 'sn-catalog-wizard-stacked-step is-selected'
						: 'sn-catalog-wizard-stacked-step';
				return step.hidden ? (
					<Fragment />
				) : (
					<Fragment>
						<button
							className={stepClass}
							on-click={
								step.hidden || step.id === selectedStep
									? undefined
									: () =>
											dispatch(WIZARD_STACKED_STEP_CLICKED, {stepId: step.id})
							}
							hook-insert={({elm}) => updateState({[`ref${index}`]: elm})}
							hook-destroy={() => updateState({[`ref${index}`]: null})}
							role="tab">
							<div className="step-label-container">
								<span className="step-name">{step.label}</span>
							</div>
							{step.iconColor ? (
								<div className={step.iconColor}>
									<now-icon icon={step.icon} size="sm" />
								</div>
							) : (
								<Fragment />
							)}
						</button>
						{_renderTooltip(
							state[`ref${index}`],
							index,
							step.label,
							['bottom-center top-center'],
							200
						)}
					</Fragment>
				);
			})}
		</div>
	);
};

export default view;
