import {
	ACTION_TYPES,
	SUBMITTED,
	SUBMIT_FAILED,
	SAVED,
	ITEMS_SUBMIT_URL,
	MODE_TYPES,
	TEMPLATE_SUBMIT_URL,
	FETCH_SESSION_MESSAGES_FAILED,
	RENDER_NOTIFICATIONS_REQUESTED,
	FETCH_USER_DATA_SUCCEEDED,
	FETCH_USER_DATA_FAILED,
} from './constants';

import {createGraphQLEffect} from '@servicenow/ui-effect-graphql';
import {sessionNotificationsQuery, userDataQuery} from './queryUtils';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import _ from 'lodash';
export const createBeforeSubmitInterceptor = (context) => {
	const {coeffects} = context;
	const {state, action, properties} = coeffects;
	const {
		payload: {actionType, mode},
	} = action;
	const templateMode = !(_.isEmpty(mode) || mode == MODE_TYPES.NORMAL);
	const httpEffect = {
		...createHttpEffect(templateMode ? TEMPLATE_SUBMIT_URL : ITEMS_SUBMIT_URL, {
			method: 'POST',
			batch: false,
			pathParams: templateMode
				? ['sysId', 'recordId']
				: ['sysId', 'actionName'],
			queryParams: templateMode ? ['mode', 'templateId'] : [],
			dataParam: 'data',
			successActionType: ACTION_TYPES.SAVE === actionType ? SAVED : SUBMITTED,
			errorActionType: SUBMIT_FAILED,
		}),
	};

	coeffects.action = {
		...action,
		payload: {
			...(action.payload || {}),
			...state.itemHandler.getSubmitPayload(state, properties, actionType),
		},
	};

	return {
		...context,
		coeffects,
		effects: [
			{
				effect: httpEffect.effect,
				args: [...httpEffect.args, {...coeffects}],
			},
		],
	};
};

export const fetchSessionMessages = (context) => {
	const {coeffects} = context;
	const graphqlEffect = {
		...createGraphQLEffect(sessionNotificationsQuery, {
			variableList: [],
			successActionType: RENDER_NOTIFICATIONS_REQUESTED,
			errorActionType: FETCH_SESSION_MESSAGES_FAILED,
		}),
	};
	return {
		...context,
		coeffects,
		effects: [
			{
				effect: graphqlEffect.effect,
				args: [...graphqlEffect.args, {...coeffects}],
			},
		],
	};
};

export const fetchUserData = (context) => {
	const {coeffects} = context;
	const graphqlEffect = {
		...createGraphQLEffect(userDataQuery(), {
			successActionType: FETCH_USER_DATA_SUCCEEDED,
			errorActionType: FETCH_USER_DATA_FAILED,
		}),
	};
	return {
		...context,
		coeffects,
		effects: [
			{
				effect: graphqlEffect.effect,
				args: [...graphqlEffect.args, {...coeffects}],
			},
		],
	};
};
