import '@devsnc/sn-form-controls';
import '@servicenow/now-heading';
import '@servicenow/now-button';
import '@servicenow/now-icon';
import '@servicenow/now-radio-buttons';
import {
	PLATFORM_URL,
	RADIO_ITEMS,
	PORTAL_PAGE_URL,
	MOBILE_PAGE_URL,
	PLATFORM,
	WORKSPACE,
	CHOICE_LABEL,
	IFRAME_TITLE,
	IFRAME_LOADED,
	PREVIEW_IN_TAB,
	MOBILE,
	PORTAL,
	SWP_PORTAL,
	DESCRIPTION_TEXT,
	ITEM_TAB_IN_PREVIEW,
	MODAL,
} from './constants';
import {t} from 'sn-translate';

const view = (
	{properties: {source = MODAL, sysId, portal = SWP_PORTAL}, uiInterface, url},
	dispatch
) => {
	return (
		<div className="sn-catalog-preview">
			<now-rich-text
				html={DESCRIPTION_TEXT}
				className="sn-preview-page-heading-container-title"></now-rich-text>
			<div className="sn-preview-page">
				<div className="sn-preview-page-side">
					<div className="sn-preview-page-content-grid-selector">
						<div className="sn-preview-page-content-grid-selector-picker">
							<now-radio-buttons
								label={CHOICE_LABEL}
								options={RADIO_ITEMS}
								helperContent={t(
									'This preview is representational and may differ based on the style settings of your portal or mobile application.'
								)}
							/>
						</div>
						<div className="sn-preview-page-content-grid-selector-textlink">
							{getTextLink(source, url)}
						</div>
					</div>
				</div>
				{getPreviewPageContainer(
					{source, sysId, portal},
					{url, uiInterface},
					dispatch
				)}
			</div>
		</div>
	);
};

const getPreviewPageContainer = (
	{sysId = '', portal = SWP_PORTAL},
	{uiInterface = PORTAL},
	dispatch
) => {
	return (
		<div className="sn-preview-page-content-grid">
			<div className="sn-preview-page-content-grid-view-content">
				{getContent(uiInterface, sysId, portal, dispatch)}
			</div>
		</div>
	);
};

/**
 * For now opening the URL in a new tab
 */
const getTextLink = (source, url) => {
	if (source === MODAL) {
		return (
			<div>
				<now-button-bare
					variant="primary"
					size="sm"
					append-to-payload={{type: ITEM_TAB_IN_PREVIEW, url}}>
					{getPreviewInTabLabel()}
				</now-button-bare>
			</div>
		);
	}
	return null;
};

const getPreviewInTabLabel = () => {
	return (
		<div>
			{PREVIEW_IN_TAB}&nbsp;&nbsp;
			<now-icon icon="open-link-fill" size="sm" />
		</div>
	);
};

const getURL = (uiInterface, sysId, portal = SWP_PORTAL) => {
	switch (uiInterface) {
		case WORKSPACE:
			return '';
		case PLATFORM:
			return PLATFORM_URL + sysId;
		case MOBILE:
			return portal + MOBILE_PAGE_URL + sysId;
		default:
			return portal + PORTAL_PAGE_URL + sysId;
	}
};

const getContent = (uiInterface, sysId, portal = SWP_PORTAL, dispatch) => {
	switch (uiInterface) {
		case WORKSPACE:
			return getWorkspaceContent(sysId);
		default:
			return getPlatformOrPortalContent(
				getURL(uiInterface, sysId, portal),
				dispatch
			);
	}
};

const getWorkspaceContent = () => <div />;

const getPlatformOrPortalContent = (url, dispatch) => {
	return (
		<iframe
			on-load={(evt) => {
				dispatch(IFRAME_LOADED, {elm: evt.target});
			}}
			src={url}
			width="100%"
			height="100%"
			frameBorder="0"
			sandbox="allow-scripts allow-popups allow-same-origin allow-modals"
			title={IFRAME_TITLE}
		/>
	);
};

export default view;
