import {createCustomElement} from '@servicenow/ui-core';
import {snabbdom} from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import {
	NOW_BUTTON_BARE_CLICKED,
	NOW_BUTTON_CLICKED,
	WIZARD_SUBMIT_CLICKED,
	STACKED,
	WIZARD_STEP_SELECTED,
} from '../constants';
createCustomElement('sn-catalog-wizard-step-details', {
	renderer: {type: snabbdom, view},
	properties: {
		formData: {
			default: {},
		},
		wizardSysId: {
			default: '',
		},
		stepDetails: {
			default: {},
		},
		nextStep: {
			default: '',
		},
		layout: {
			default: STACKED,
		},
		submitting: {
			default: false,
		},
	},
	actionHandlers: {
		[NOW_BUTTON_BARE_CLICKED]: ({action: {payload}, dispatch}) => {
			if (payload.action === WIZARD_STEP_SELECTED) {
				dispatch(payload.action, {value: payload.value});
			}
		},
		[NOW_BUTTON_CLICKED]: ({action: {payload}, dispatch}) => {
			if (payload.action === WIZARD_SUBMIT_CLICKED) {
				dispatch(payload.action);
			}
		},
	},
	styles,
});
