import {createCustomElement} from '@servicenow/ui-core';
import {snabbdom} from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import {STACKED} from '../constants';
import {actions, actionHandlers} from './actions';

createCustomElement('sn-catalog-wizard-steps', {
	renderer: {type: snabbdom, view},
	properties: {
		steps: {
			default: [],
		},
		selectedStep: {
			default: '',
		},
		layout: {
			default: STACKED,
		},
	},
	styles,
	actions,
	actionHandlers,
});
