import {STACKED, TABBED} from '../constants';
import '../wizard-step-section';
import _ from 'lodash';

export const getStepSections = (sections, stepIndex, wizardData, formData) => {
	let stepSectionsClassName = 'sn-catalog-wizard-step-sections-detail-';
	stepSectionsClassName += wizardData.layout === STACKED ? STACKED : TABBED;
	return (
		<div className={stepSectionsClassName}>
			{sections.map((section, sectionId) => {
				if (!_.isEmpty(section.questions) && section.visible) {
					const {width} = section;
					let sectionStyle = section.style || '';
					var layoutClass;
					if (wizardData.layout == TABBED) {
						layoutClass = 'one-col-tabbed';
						sectionStyle = 'flat';
					} else {
						layoutClass = width === '50' ? 'two-col' : 'one-col';
					}
					return (
						<div
							className={
								'sn-catalog-wizard-step-section-' +
								layoutClass +
								' ' +
								sectionStyle
							}>
							<sn-catalog-wizard-step-section
								formData={formData}
								wizard-data={wizardData}
								section-data={section}
								section-id={sectionId}
								section-style={sectionStyle}
								step-id={stepIndex}></sn-catalog-wizard-step-section>
						</div>
					);
				}
			})}
		</div>
	);
};
