import {catalogWizardMessages} from '../../messages';
export const beforeUnloadListener = (e) => {
	e.preventDefault();
	e.returnValue = catalogWizardMessages.WIZARD_DIRTY_FORM_ALERT_MESSAGE;
};

export const updateAESTabDirty = (tabDirty) => {
	window.frameElement.appEngineStudioBuilderAPI.updateTabState({
		hasUnsavedChanges: tabDirty,
	});
};

export const isAESTab = () => {
	return (
		window.frameElement &&
		window.frameElement.appEngineStudioBuilderAPI &&
		window.frameElement.appEngineStudioBuilderAPI.isAppEngineStudio
	);
};
