import {
	WIZARD_STACKED_STEP_CLICKED,
	TAB_ITEM_CLICKED,
	WIZARD_STEP_SELECTED,
} from '../constants';

export const actions = {
	[WIZARD_STACKED_STEP_CLICKED]: {
		private: true,
	},
	[TAB_ITEM_CLICKED]: {
		private: true,
	},
};
export const actionHandlers = {
	[TAB_ITEM_CLICKED]: ({action, dispatch}) => {
		dispatch(WIZARD_STEP_SELECTED, {value: action.payload.item.id});
	},
	[WIZARD_STACKED_STEP_CLICKED]: ({action, dispatch}) => {
		dispatch(WIZARD_STEP_SELECTED, {value: action.payload.stepId});
	},
};
