import {createCustomElement} from '@servicenow/ui-core';
import {snabbdom} from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
createCustomElement('sn-catalog-wizard-step-section', {
	renderer: {type: snabbdom, view},
	properties: {
		formData: {
			default: '',
		},
		wizardData: {
			default: {},
		},
		sectionData: {
			default: {},
		},
		sectionId: {
			default: 0,
		},
		stepId: {
			default: 0,
		},
		sectionStyle: {
			default: 'card',
		},
		readOnlyMode: {
			default: 'default',
		},
	},
	styles,
});
