import {cloneDeep} from 'lodash';

export const createField = (feedback, formData) => {
	let fields = {
		[feedback.macroponent]: {
			macroponentId: feedback.macroponent,
			name: feedback.macroponent,
			type: 'macro',
			visible: true,
			formData: cloneDeep(formData),
		},
	};
	return fields;
};

export const createLayout = (feedback) => {
	return [
		{
			name: feedback.macroponent,
			type: 'field',
		},
	];
};
