import ProducerItemHandler from './ProducerItemHandler';
import TemplateItemHandler from './templateItemHandler';
import ItemHandler from './itemHandler';
import {isEmpty} from 'lodash';
import {MODE_TYPES} from '../constants';

/**
 *
 * @param {String} mode - mode used in wizard
 * @param {Function} dispatch - Dispatch function
 * @returns {ItemHandler}
 */
const createItemHandler = (mode) => {
	if (isEmpty(mode) || mode == MODE_TYPES.NORMAL) {
		return new ProducerItemHandler();
	} else if (mode == MODE_TYPES.TEMPLATE) {
		return new TemplateItemHandler();
	}
	return new ItemHandler();
};
export default createItemHandler;
