import {actionTypes} from '@servicenow/ui-core';
import {
	WIZARD_STEPS_NOT_DEFINED,
	WIZARD_STEP_SELECTED,
	WIZARD_CONTENT_CHANGED,
	NOW_BUTTON_CLICKED,
	SAVE_CLICKED,
	SUBMIT_CLICKED,
	WIZARD_SUBMIT_CLICKED,
	WIZARD_SAVE_CLICKED,
	WIZARD_PREVIEW_CLICKED,
	NOW_MODAL_OPENED_SET,
	BUTTON_BARE_CLICKED,
	ITEM_TAB_IN_PREVIEW,
	WIZARD_HOME_CLICKED,
	NOW_BUTTON_ICONIC_CLICKED,
	NOW_MODAL_FOOTER_ACTION_CLICKED,
	WIZARD_SETTINGS_ITEM_CLICKED,
	NOW_DROPDOWN_ITEM_CLICKED,
	DELETE_CLICKED,
	WIZARD_DELETE_MODAL_FOOTER_CLICKED,
	DELETE_MODAL_CONFIRM_ID,
	WIZARD_HOMEPAGE_MODAL_FOOTER_CLICKED,
	DELETE_ID,
} from './constants';
import {getNextStep} from './util';
import {catalogWizardMessages} from '../../messages';

const {COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED} = actionTypes;

export default {
	[COMPONENT_BOOTSTRAPPED]: ({dispatch}) => {
		dispatch(WIZARD_CONTENT_CHANGED);
	},

	[COMPONENT_PROPERTY_CHANGED]: ({action, dispatch, updateState}) => {
		const {
			payload: {name, value = {}, previousValue = {}},
		} = action;
		if (name === 'formData') {
			if (value.sysId !== previousValue.sysId) {
				updateState({
					currentSelectedStep: 0,
					showPreview: false,
					homePageAlertOpened: false,
				});
			}
		}
		dispatch(WIZARD_CONTENT_CHANGED);
	},

	[NOW_BUTTON_CLICKED]: ({dispatch, action: {payload = {}}}) => {
		if (payload.action) {
			dispatch(payload.action, payload);
		}
	},

	[NOW_BUTTON_ICONIC_CLICKED]: ({dispatch, action: {payload = {}}}) => {
		if (payload.action) {
			dispatch(payload.action, payload);
		}
	},

	[NOW_MODAL_FOOTER_ACTION_CLICKED]: ({updateState, action, dispatch}) => {
		if (action.payload.action === WIZARD_DELETE_MODAL_FOOTER_CLICKED) {
			if (action.payload.footerAction.id == DELETE_MODAL_CONFIRM_ID) {
				dispatch(DELETE_CLICKED);
			}
			updateState({
				showDeleteModal: false,
			});
		} else if (action.payload.action === WIZARD_HOMEPAGE_MODAL_FOOTER_CLICKED) {
			updateState({
				homePageAlertOpened: false,
			});
		}
	},

	[NOW_DROPDOWN_ITEM_CLICKED]: ({action, updateState}) => {
		action.stopPropagation();
		if (action.payload.action === WIZARD_SETTINGS_ITEM_CLICKED) {
			if (action.payload.item.id === DELETE_ID) {
				updateState({
					showDeleteModal: true,
				});
			}
		}
	},

	[WIZARD_PREVIEW_CLICKED]: ({updateState, dispatch}) => {
		dispatch(SAVE_CLICKED);
		updateState({
			showPreview: true,
		});
	},

	[BUTTON_BARE_CLICKED]: ({
		action: {
			stopPropagation,
			payload: {type = ''},
		},
		updateState,
	}) => {
		if (type === ITEM_TAB_IN_PREVIEW) {
			stopPropagation();
			updateState({
				showPreview: false,
			});
		}
	},

	[NOW_MODAL_OPENED_SET]: ({updateState}) => {
		updateState({
			showPreview: false,
			homePageAlertOpened: false,
			showDeleteModal: false,
		});
	},

	[WIZARD_CONTENT_CHANGED]: ({
		updateState,
		properties,
		state,
		dispatch,
		action,
	}) => {
		action.stopPropagation();
		const {steps, stepsList} = properties;
		const {currentSelectedStep} = state;
		if (!steps || !steps.length) {
			dispatch(WIZARD_STEPS_NOT_DEFINED);
			return;
		}
		const filteredSteps = stepsList.filter((step) => !step.hidden);
		if (!filteredSteps.length) {
			dispatch(WIZARD_STEPS_NOT_DEFINED);
			return;
		}

		if (stepsList[currentSelectedStep].hidden) {
			const nextCurrentStep = getNextStep(stepsList, currentSelectedStep);
			let nextNextStep;
			if (nextCurrentStep >= 0) {
				nextNextStep = getNextStep(stepsList, nextCurrentStep);
			}
			updateState({
				currentSelectedStep: nextCurrentStep,
				nextStep: nextNextStep,
			});
			return;
		}
		const nextStep = getNextStep(stepsList, currentSelectedStep);
		updateState({
			...state,
			nextStep,
		});
	},

	[WIZARD_STEPS_NOT_DEFINED]: ({updateState}) => {
		updateState({
			isError: true,
			message: catalogWizardMessages.STEPS_NOT_DEFINED,
		});
	},

	[WIZARD_SAVE_CLICKED]: {
		effect: ({dispatch}) => {
			dispatch(SAVE_CLICKED);
		},
		stopPropagation: true,
	},

	[WIZARD_SUBMIT_CLICKED]: {
		effect: ({dispatch}) => {
			dispatch(SUBMIT_CLICKED);
		},
		stopPropagation: true,
	},

	[WIZARD_STEP_SELECTED]: ({properties, action, updateState, dispatch}) => {
		action.stopPropagation();
		let id = action.payload.value;
		const {stepsList, wizardMeta = {}} = properties;
		const nextStep = getNextStep(stepsList, id);
		updateState({
			currentSelectedStep: id,
			nextStep,
		});
		if (wizardMeta.showSave) {
			dispatch(SAVE_CLICKED);
		}
	},

	[WIZARD_HOME_CLICKED]: ({properties, updateState}) => {
		const {formDirty} = properties;
		if (formDirty) {
			updateState({
				homePageAlertOpened: true,
			});
		}
	},
};
