import {actionTypes} from '@servicenow/ui-core';
import {createHttpEffect} from '@servicenow/ui-effect-http';
import {isEmpty, cloneDeep, pick} from 'lodash';
import {parseWizardData} from '../parseWizardData';
import {createInitializeCatalogFormEnvironmentEffect} from '@devsnc/library-catalog-form';

import {
	WIZARD_RESOURCE_FETCH_SUCCEEDED,
	WIZARD_RESOURCE_REQUESTED,
	WIZARD_RESOURCE_FETCH_FAILED,
	WIZARD_DATA_GET_URL,
	FORM_ENVIRONMENT_CREATED,
	FORM_ENVIRONMENT_CREATION_FAILED,
	RESOURCES_PROCESSED,
	VARIABLES_UPDATED,
	SUBMIT_VALIDATED,
	NEW_MESSAGE_ARRIVED,
	MESSAGE_TYPES,
	ERROR_FETCHING_RESOURCE,
	FORM_ENVIRONMENT_CREATED_DATA_FETCH_MODE,
	FORM_ENVIRONMENT_CREATION_FAILED_DATA_FETCH_MODE,
	WIZARD_RESOURCE_FETCH_FAILED_DATA_FETCH_MODE,
	WIZARD_HOME_ALERT_MODAL_CONTINUE_ID,
	REDIRECT_TO_BUILDER_HOME,
	NOW_MODAL_FOOTER_ACTION_CLICKED,
	WIZARD_HOME_CLICKED,
	NOW_BUTTON_ICONIC_CLICKED,
	WIZARD_RESOURCE_FETCH_STARTED,
	WIZARD_TITLE_CHANGED,
	WIZARD_ROUTE_PARAMS_CHANGED,
	CANVAS_CONTENT_UPDATED,
	WIZARD_HOMEPAGE_MODAL_FOOTER_CLICKED,
	DELETE_CATALOG_ITEM,
	DELETE_CLICKED,
	CATALOG_ITEM_DELETE_URL,
	DELETE_SUCCESS,
	DELETE_FAILED,
	NOW_UXF_PAGE_SET_LOADING_STATE,
	MODE_TYPES,
	SC_TEMPLATE,
	FETCH_USER_DATA_REQUESTED,
	SC_WIZARD_BROWSER_TITLE_CHANGED,
} from '../constants';
import createItemHandler from '../itemHandlers/itemHandlerFactory';
import {beforeUnloadListener} from '../utils';
import {fetchSessionMessages} from '../interceptors';
const {COMPONENT_BOOTSTRAPPED, COMPONENT_PROPERTY_CHANGED} = actionTypes;
const fetchWizardData = createHttpEffect(WIZARD_DATA_GET_URL, {
	method: 'GET',
	pathParams: ['sys_id'],
	queryParams: [
		'mode',
		'template_id',
		'record_id',
		'sysparm_parent_target_record_table',
		'sysparm_parent_target_record_id',
	],
	startActionType: WIZARD_RESOURCE_FETCH_STARTED,
	successActionType: WIZARD_RESOURCE_FETCH_SUCCEEDED,
	errorActionType: WIZARD_RESOURCE_FETCH_FAILED,
});

const deleteCatalogItem = createHttpEffect(CATALOG_ITEM_DELETE_URL, {
	method: 'DELETE',
	pathParams: ['record_id'],
	queryParams: [''],
	successActionType: DELETE_SUCCESS,
	errorActionType: DELETE_FAILED,
});

const getFormData = (data) => {
	const getAllFields = (wizard) => {
		let steps = wizard.steps;
		let allQuestions = {};
		steps.forEach((step) => {
			if (!step.isCustom) {
				step.sections.forEach((section) => {
					if (section.questions) {
						allQuestions = {
							...allQuestions,
							...section.questions,
						};
					}
				});
			}
		});
		return allQuestions;
	};

	const formData = {
		...formData,
		client_scripts: data.clientScripts,
		ui_policy: data.policies,
		variablesLayout: {},
		data_lookups: data.data_lookups || {},
		fields: getAllFields(data),
		sourceTable: data.targetTable,
		sourceId: data.targetRecordData.targetRecord,
	};

	return {
		formData,
	};
};

export default {
	[COMPONENT_BOOTSTRAPPED]: ({dispatch, properties}) => {
		const {
			sysId,
			mode,
			templateId,
			recordId,
			parentRecordTable,
			parentRecordId,
		} = properties;
		if (isEmpty(sysId)) {
			dispatch(WIZARD_RESOURCE_FETCH_FAILED);
			return;
		}
		dispatch(WIZARD_RESOURCE_REQUESTED, {
			sys_id: sysId,
			mode: mode,
			template_id: templateId,
			record_id: recordId,
			sysparm_parent_target_record_table: parentRecordTable,
			sysparm_parent_target_record_id: parentRecordId,
		});
	},
	[COMPONENT_PROPERTY_CHANGED]: ({dispatch, action, properties}) => {
		const {
			payload: {name, value, previousValue},
		} = action;
		const {
			sysId,
			mode,
			templateId,
			recordId,
			targetRecord,
			parentRecordTable,
			parentRecordId,
			dataFetchOnly,
		} = properties;
		const watchedProperties = ['sysId', 'mode', 'templateId', 'recordId'];
		if (!watchedProperties.includes(name)) {
			return;
		}
		if (previousValue != value) {
			if (name === 'recordId') {
				if (targetRecord === recordId && !dataFetchOnly) {
					return;
				}
			}
			if (isEmpty(sysId)) {
				dispatch(WIZARD_RESOURCE_FETCH_FAILED);
				return;
			}
			dispatch(WIZARD_RESOURCE_REQUESTED, {
				sys_id: sysId,
				mode: mode,
				template_id: templateId,
				record_id: recordId,
				sysparm_parent_target_record_table: parentRecordTable,
				sysparm_parent_target_record_id: parentRecordId,
			});
		}
	},
	[WIZARD_RESOURCE_REQUESTED]: fetchWizardData,
	[WIZARD_RESOURCE_FETCH_SUCCEEDED]: ({
		action,
		dispatch,
		updateState,
		updateProperties,
		properties: {mode, hideHomeButton, wizardTitle: wizardTitleFromProps},
	}) => {
		const {payload = {}} = action;
		if (payload.errors && payload.errors.length > 0) {
			dispatch(WIZARD_RESOURCE_FETCH_FAILED, payload.errors);
		} else {
			const wizardData = cloneDeep(payload.result);
			const stepsArray = wizardData.steps.map((step, index) => {
				const hidden = !(
					(step.sections && step.sections.length) ||
					(step.isCustom && step.macroponent)
				);
				return {label: step.name, hidden, sys_id: step.sysId, id: index};
			});
			wizardData.targetTable =
				mode == MODE_TYPES.TEMPLATE ? SC_TEMPLATE : wizardData.targetTable;
			parseWizardData(wizardData, stepsArray, mode);
			wizardData.stepsList = stepsArray;
			const itemDetails = {
				sysId: wizardData.producer,
				type: wizardData.type,
				shortDescription: wizardData.shortDescription,
				name: wizardData.produerName,
			};
			const wizardMeta = {
				wizardTitle: wizardData.name,
				wizardShortDescription: wizardData.shortDescription,
				layout: wizardData.layout,
				showSave: wizardData.showSave,
				showCancel: wizardData.showCancel,
				showSubmit: wizardData.showSubmit,
				hasFeedbackPanel: wizardData.hasFeedbackPanel,
				picture: wizardData.picture,
				targetRecordData: wizardData.targetRecordData,
				submitButtonLabel: wizardData.submitButtonLabel,
				previewPortal: wizardData.previewPortal || 'swp',
				hideHomeButton,
			};
			updateProperties({
				wizardDetails: itemDetails,
				targetRecord: wizardData.targetRecordData.targetRecord,
				editMode: wizardData.editMode,
				wizardData,
			});

			dispatch(WIZARD_TITLE_CHANGED, {
				value: wizardTitleFromProps || wizardData.name,
			});

			var userData =
				window.ux_globals.presource['sn-workspace-header:wsUserData'];
			if (isEmpty(userData)) {
				dispatch(FETCH_USER_DATA_REQUESTED);
			}

			updateState({
				wizardMeta,
			});

			const {formData = {}} = getFormData(wizardData);
			dispatch(RESOURCES_PROCESSED, {
				itemDetails,
				formData,
			});
		}
	},
	[RESOURCES_PROCESSED]: createInitializeCatalogFormEnvironmentEffect({
		dataParam: 'formData',
		successActionType: FORM_ENVIRONMENT_CREATED,
		errorActionType: FORM_ENVIRONMENT_CREATION_FAILED,
		formSubmittedActionType: SUBMIT_VALIDATED,
		variableUpdatedActionType: VARIABLES_UPDATED,
	}),
	[WIZARD_RESOURCE_FETCH_STARTED]: ({updateProperties, action, dispatch}) => {
		action.stopPropagation();
		updateProperties({
			isLoading: true,
			isError: false,
			isSuccess: false,
			errorMessage: '',
			submitted: false,
		});
		dispatch(NOW_UXF_PAGE_SET_LOADING_STATE, {
			id: 'non-blocking',
			loading: true,
			nonBlocking: true,
		});
	},
	[WIZARD_RESOURCE_FETCH_FAILED]: ({
		dispatch,
		updateProperties,
		action,
		properties: {dataFetchOnly},
	}) => {
		dispatch(NOW_UXF_PAGE_SET_LOADING_STATE, {
			id: 'non-blocking',
			loading: false,
		});
		if (dataFetchOnly) {
			return dispatch(WIZARD_RESOURCE_FETCH_FAILED_DATA_FETCH_MODE, {
				...action.payload,
			});
		}
		const message = ERROR_FETCHING_RESOURCE;
		updateProperties({
			isLoading: false,
			isError: true,
			isSuccess: false,
			errorMessage: message,
		});
		dispatch(NEW_MESSAGE_ARRIVED, {
			type: MESSAGE_TYPES.ERROR,
			message: message,
		});
	},
	[FORM_ENVIRONMENT_CREATED]: ({
		action,
		properties,
		updateState,
		updateProperties,
		dispatch,
	}) => {
		dispatch(NOW_UXF_PAGE_SET_LOADING_STATE, {
			id: 'non-blocking',
			loading: false,
		});
		const {formData = {}, mode, dataFetchOnly} = properties;
		if (dataFetchOnly) {
			return dispatch(FORM_ENVIRONMENT_CREATED_DATA_FETCH_MODE, {
				...action.payload,
			});
		}
		const {gForm, fields} = action.payload;
		updateProperties({
			isLoading: false,
			isError: false,
			isSuccess: true,
			variables: fields,
			formData: {
				...formData,
				sysId: gForm.getSysId(),
				tableName: gForm.getTableName(),
				encodedRecord: gForm.getEncodedRecord(),
				serializedChanges: JSON.stringify(gForm.serialize()),
				classicForm: true,
			},
		});

		const itemHandler = createItemHandler(mode);
		updateState({
			itemHandler,
			gForm,
		});
	},
	[FORM_ENVIRONMENT_CREATION_FAILED]: ({
		action,
		dispatch,
		updateProperties,
		properties: {dataFetchOnly},
	}) => {
		dispatch(NOW_UXF_PAGE_SET_LOADING_STATE, {
			id: 'non-blocking',
			loading: false,
		});
		if (dataFetchOnly) {
			return dispatch(FORM_ENVIRONMENT_CREATION_FAILED_DATA_FETCH_MODE, {
				...action.payload,
			});
		}
		const {payload = {}} = action;
		updateProperties({
			isLoading: false,
			isError: true,
			isSuccess: false,
			errorMessage: payload.error,
		});
		dispatch(NEW_MESSAGE_ARRIVED, {
			type: MESSAGE_TYPES.ERROR,
			message: payload.error,
		});
	},

	[NOW_MODAL_FOOTER_ACTION_CLICKED]: ({
		action: {
			payload: {
				action,
				footerAction: {id},
			},
		},
		dispatch,
	}) => {
		if (
			action === WIZARD_HOMEPAGE_MODAL_FOOTER_CLICKED &&
			id === WIZARD_HOME_ALERT_MODAL_CONTINUE_ID
		) {
			window.removeEventListener('beforeunload', beforeUnloadListener);
			dispatch(REDIRECT_TO_BUILDER_HOME);
		}
	},

	[NOW_BUTTON_ICONIC_CLICKED]: ({
		properties,
		dispatch,
		action: {payload = {}},
	}) => {
		const {formDirty} = properties;
		const {action} = payload;
		if (!formDirty && action && action === WIZARD_HOME_CLICKED) {
			dispatch(REDIRECT_TO_BUILDER_HOME);
		}
	},
	[DELETE_CLICKED]: ({
		dispatch,
		state: {
			wizardMeta: {
				targetRecordData: {targetRecord},
			},
			properties: {editMode},
		},
		action,
	}) => {
		action.stopPropagation();
		if (editMode) {
			dispatch(DELETE_CATALOG_ITEM, {
				record_id: targetRecord,
			});
		}
		dispatch(REDIRECT_TO_BUILDER_HOME);
	},
	[DELETE_CATALOG_ITEM]: deleteCatalogItem,

	[DELETE_FAILED]: {
		stopPropagation: true,
		interceptors: [fetchSessionMessages],
	},
	[DELETE_SUCCESS]: {
		stopPropagation: true,
		interceptors: [fetchSessionMessages],
	},

	[WIZARD_ROUTE_PARAMS_CHANGED]: ({action, dispatch, properties}) => {
		action.stopPropagation();
		const {
			targetRecord: recordId = '',
			mode = 'normal',
			templateId = '',
		} = properties;
		const payload = pick(action.payload, ['recordId', 'mode', 'templateId']);
		if (!isEmpty(action.payload)) {
			dispatch(CANVAS_CONTENT_UPDATED, {
				params: {
					recordId,
					mode,
					templateId,
					...payload,
				},
				redirect: true,
			});
		}
	},

	[SC_WIZARD_BROWSER_TITLE_CHANGED]: ({
		dispatch,
		action,
		properties: {wizardTitle: wizardTitleFromProps},
		state: {
			wizardMeta: {wizardTitle},
		},
	}) => {
		dispatch(WIZARD_TITLE_CHANGED, {
			value: action.payload.value || wizardTitleFromProps || wizardTitle,
		});
	},
};
