import ItemHandler from './itemHandler';
import {ACTION_TYPES, SAVE_PRODUCER, SUBMIT_PRODUCER} from '../constants';

export default class ProducerItemHandler extends ItemHandler {
	getSubmitPayload(state = {}, properties = {}, actionType) {
		const {gForm} = state;
		const {
			wizardDetails,
			targetRecord,
			editMode,
			templateId,
			parentRecordTable,
			parentRecordId,
		} = properties;

		let sysparm_target_id = '';
		let sysparm_edit_mode = 'false';
		if (editMode) {
			sysparm_edit_mode = 'true';
			sysparm_target_id = targetRecord;
		}
		const payload = {
			sysId: wizardDetails.sysId,
			data: {
				variables: (gForm.serialize() || []).reduce((acc, field) => {
					acc[field.variable_name] = field.value;
					return acc;
				}, {}),
				sysparm_item_guid: targetRecord,
				sysparm_edit_mode,
				sysparm_target_id,
				sysparm_template_id: templateId,
				sysparm_parent_target_record_table: parentRecordTable,
				sysparm_parent_target_record_id: parentRecordId,
			},
			actionName:
				actionType === ACTION_TYPES.SAVE ? SAVE_PRODUCER : SUBMIT_PRODUCER,
		};
		return payload;
	}
}
